// Replacement methods
var replacement = {
    // Stars replacement: Create a string of '*' with the same length as the word
    stars: function (wordLength) {
        return "*".repeat(wordLength);
    },
    
    // Grawlix replacement: Create a random string of characters from grawlixChars with the same length as the word
    grawlix: function (wordLength) {
        var r, n = "";
        for (let i = 0; i < wordLength; i++) {
            r = Math.floor(Math.random() * grawlixChars.length);
            n += grawlixChars[r];
        }
        return n;
    }
};

// Expose the functions on the window object so they can be accessed globally
window.ProfanityFilter = {
    // Function to clean a string by replacing profane words
    clean: function (text) {
        var t;
        
        // Loop over each word in the dictionary
        dictionary.forEach(function(r) {
            // Create a regex pattern to match whole words (case-insensitive)
            const wordPattern = new RegExp('\\b' + r + '\\b', 'gi');
            if (wordPattern.test(text)) {
                const wordLength = r.length;  // Get the length of the matched word
                t = replacement[replacementMethod](wordLength);  // Call the replacement function with the word's length
                text = text.replace(wordPattern, t);  // Replace the profane word with the generated replacement
            }
        });
        
        return text;  // Return the cleaned text
    },

    // Seed the dictionary with custom words
    seed: function (newDictionary) {
        if (Array.isArray(newDictionary)) {
            dictionary = newDictionary;
        } else {
            console.warn("The seed function expects an array. Can't load seed file in the browser.");
        }
    },

    // Set the replacement method (either 'stars' or 'grawlix')
    setReplacementMethod: function (method) {
        if (typeof replacement[method] === "undefined") {
            throw new Error('Replacement method "' + method + '" not valid.');
        }
        replacementMethod = method;
    },

    // Set custom grawlix characters
    setGrawlixChars: function (chars) {
        grawlixChars = chars;
    },

    // Add a new word to the profanity dictionary
    addWord: function (word) {
        dictionary.push(word.toLowerCase());
    },

    // Remove a word from the profanity dictionary
    removeWord: function (word) {
        dictionary = dictionary.filter(function(item) {
            return item !== word.toLowerCase();
        });
    },

    // Debug function to return current state
    debug: function () {
        return { dictionary: dictionary, replacementMethod: replacementMethod, grawlixChars: grawlixChars };
    }
};
